<template>
  <div>
    <city-header></city-header>
    <city-search :cities="cities"></city-search>
    <city-list :cities="cities" :hotCities="hotCities" :letter="letter"></city-list>
    <city-alphabet :cities="cities" @citychange="handleChange"></city-alphabet>
  </div>
</template>

<script>
import CityHeader from "./components/Header.vue"
import CitySearch from "./components/Search.vue"
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'
import axios from "axios" //ajax
export default {
  name: 'Citye',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      letter: ''   //List.vue
    }
  },
  methods: {
    getCityData () {
      //staticjsonstatic
      //api
      //webpakc config/index.jsproxyTable
      axios.get('/api/city.json')
        .then(this.cityDataSuccess)
    },
    cityDataSuccess (res) {
      let cityData = res.data
      if ( cityData.ret && cityData.data) {
        this.cities = cityData.data.cities
        this.hotCities = cityData.data.hotCities
      }
    },
    handleChange (e) {
      //
      //let alphabet = e.target.innerText
      this.letter = e
    }
  },
  mounted () {
    //ajax
    this.getCityData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



