<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom"></div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.curentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom"></div>
        <div class="button-list">
          <div class="button-wrapper" v-for="item of hotCities" :key="item.id" @click="setCity(item.name)">
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area" v-for="(item, key) of cities" :key="key" :ref="key">
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list" v-for="city of item" :key="city.id" @click="setCity(city.name)">
          <div class="item border-bottom">{{city.name}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
//this.$store.state.city
import { mapState, mapMutations } from "vuex"
export default {
  name: 'CityList',
  props: {
    cities: Object,
    hotCities: Array,
    letter: String
  },
  computed: {
    //mapState
    //vuexcitycurentCity
    ...mapState({
      curentCity: 'city'
    })
  },
  methods: {
    setCity (city) {
      //
      //vuexdispatchactionstore/index.js
      //this.$store.dispatch("changeCity", city)
      //mutations
      //this.$store.commit("setCity", city)
      //vuexmapMutations
      this.changeCity(city)
      //
      this.$router.push("/")
    },
    ...mapMutations(['changeCity'])
  },
  mounted () {
    //better-scroll
    this.scroll = new BScroll(this.$refs.wrapper)
  },
  watch: {
    letter () {
      //letter
      if(this.letter) {
        //BScroll
        let scrollArea = this.$refs[this.letter]
        this.scroll.scrollToElement(scrollArea[0])
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~style/varibles.styl'
  .border-topbottom
    &:before
      border-color: #ccc
    &:after
      border-color: #ccc
  .border-bottom
    &:before
      border-color: #ccc
  .list
    overflow: hidden
    position: absolute
    top: 1.58rem
    left: 0
    right: 0
    bottom: 0
    .title
      line-height: .54rem
      background: #eee
      padding-left: .2rem
      color: #666
      font-size: .26rem
    .button-list
      overflow: hidden
      padding: .1rem .6rem .1rem .1rem
      .button-wrapper
        float: left
        width: 33.33%
        .button
          margin: .1rem
          padding: .1rem 0
          text-align: center
          border: .02rem solid #ccc
          border-radius: .06rem
    .item-list
      .item
        line-height: .76rem
        padding-left: .2rem
</style>



